        const res = await axios.get(
          `https://oe.datalisk.io/teams/statsBySplit/${cell.team1Id}`,
          {
            headers: {
              "x-api-key": "f561197a-82ea-4e54-acd2-386979018a7a",
            },
          }
        );
        console.log("res", res);

