import React, { useState, useEffect, useMemo } from "react";
import { useStyles, Container, Header, Body } from "./styles";
import { useParams } from "react-router-dom";
import Layout from "../../components/Layout";
import TableHistory from "../../components/TableHistory";
import "../../assets/extra.css?v=1.9.0";
import "../../assets/lol.css?v=1.9.0";

import { Month, Week } from "./utils";
import axios from "axios";
import api from "../../api";
const Managers = () => {
  const classes = useStyles();
  const [matches, setMatches] = useState([]);
  const [team, setTeam] = useState();
  let { teamName } = useParams();

  console.log("teamName", teamName);

  useEffect(() => {
    async function fetchData() {
      const res2 = await axios.get(
        `https://oe.datalisk.io/teams/statsByYear/${teamName}`,
        {
          headers: {
            "x-api-key": "f561197a-82ea-4e54-acd2-386979018a7a",
          },
        }
      );

      const res = await axios.get(
        `https://oe.datalisk.io/teams/gameDetails/${teamName}`,
        {
          headers: {
            "x-api-key": "f561197a-82ea-4e54-acd2-386979018a7a",
          },
        }
      );

      setMatches(res?.data);
      setTeam(res2?.data);
    }
    fetchData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [teamName]);
  return (
    <div>
      <div className={classes.root}>
        <Layout />
        <main className={classes.content}>
          <div className={classes.toolbar} />
          <Container>
            <div style={{ marginBottom: "75px" }}>
              <Header color="rose">
                <h1 className={classes.cardTitleWhite}>{team?.name}</h1>
                <h3 className={classes.cardCategoryWhite}>
                  {team?.league} - {team?.region}
                </h3>
              </Header>
              <Body>
                <TableHistory
                  tableHeaderColor="primary"
                  tableData={matches}
                  team={team}
                />
              </Body>
            </div>
          </Container>
        </main>
      </div>
    </div>
  );
};

export default Managers;
